
import React, { Component } from 'react';

import 'bootstrap/dist/css/bootstrap.min.css';
import { FormControl, InputGroup, Button, ListGroup, Accordion, Card, ButtonGroup, CardGroup, CardDeck } from 'react-bootstrap';

class RNG extends Component {
  constructor(props){
    super(props)

    this.state={
      number:"---",
      stop:true,

    }
  }
  spin(){
    this.setState({stop:false});
    this.setState({number:getRandomInt(1,100)})
  }
  stop(){
    this.setState({stop:true});
  }
  componentDidMount() {
    this.interval = setInterval(() => this.setState({ time: Date.now() }), 100);
  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }
  render(){
    return (
      <div className="RNG" 
        style={{
          margin: "20px",
          textAlign:"center",
          
        }}
      >
        <Card>
          <Card.Header><h1>
            {this.state.stop?this.state.number:getRandomInt(1,100)}
          </h1></Card.Header>
          <Card.Body>
            <ButtonGroup>
              <Button
                style={{marginLeft: "20px"}}
                variant='outline-success'
                onClick={() => this.spin()}
              >
                Spin
              </Button>
              <Button
                style={{marginRight: "20px"}}
                variant='outline-danger'
                onClick={() => this.stop()}
              >
                Stop
              </Button>
            </ButtonGroup>
          </Card.Body>
        </Card>
      </div>
    )
  }
}

class Unit extends Component {
  constructor(props){
    super(props);
    
    
    this.buttonClick = this.buttonClick.bind(this)
  }
  
  buttonClick = (n) => {
    this.props.parentCallback(n);

  }

  render() {
    const list = [...this.props.choices];
    let t = this
    return (
      <div className="Unit">
        <div
          style={{
            border: "2px solid",
            margin: "20px"
          }}
        >
          <CardDeck style={{margin: "20px"}}>
            <Card>
              <Card.Header><h1 style={{marginLeft: "20px"}}>{this.props.heading}</h1></Card.Header>
              <Card.Body><p style={{marginLeft: "20px"}}>{this.props.body}</p></Card.Body>
            </Card>
            {this.props.rng?<RNG/>:null}
          </CardDeck>
          <CardGroup style={{margin: "20px", padding:"20px"}}>
            {list[0].length>=1?list.map(function(item, index){
                      return <Card>
                            <Card.Body><h4>{item[2]}</h4></Card.Body>
                            <Button 
                                variant='outline-primary'
                                onClick={() => {t.buttonClick(item[1])
                                }}
                            >
                            
                            {item[0]}
                            </Button></Card>;
                  
                    }
                    ):null
            }
          </CardGroup>
        <br/>
        </div>
        
      </div>
    )
  }
}

