class App extends Component {
  constructor(props){
    super(props);


    this.defaultstate={
      current: <Unit 
        parentCallback={this.switchUnit}
        heading="World War 1 Simulation" 
        body="What follows is a simulation of WW1 France. You will take the role of a Captain in the American Army in September 1918. You are in command of a company of infantry. You lead 256 men in four platoons of infantry and a fifth platoon with three machine gunners and their crews. (The machine guns are heavy but can be carried). Your unit is made up of African Americans.  President Wilson tried hard to keep America out of the war and he refused to allow the Army to equip for it before we entered the war. Therefore, like many African American units you are actually fighting wearing French Uniforms and with mostly French equipment.  You are about to go into battle for the first time. 4 months ago you were working a job but because of your college education you were given a command." 
        choices={[
          ["Orders",
          <Unit 
            parentCallback={this.switchUnit}
            heading="The Orders" 
            body="Your orders are to capture the above German Concrete Bunker which houses two machine guns. The bunker is part of the second or main line of German trenches. To get to it you must cross 700 yards of muddy shell holed no man's land, get through a web or “belt” of barbed wire that is 30 feet across, then drive the Germans from the first (front line trench). Finally you take the second trench line and the bunker (your objective). You are to hold this locally important bunker and wait for the follow up Battalion to reinforce you, or if the opportunity exists try to punch through the next German line and the one that follows that one. But your objective is to seize and hold the bunker. The French failed to take this very same bunker in June 1916." 
            choices={[
              ['To First Decision',
              <Unit 
                parentCallback={this.switchUnit}
                heading="Use of Artillery" 
                body="Your Lt. Col. (commanding officer) gave you no detailed directions. It is up to you to figure out how to take the bunker. Your first decision is how do you use artillery? Read each option and click on the one you choose. You have a French advisor who fought here in 1916. You have three choices and some advice." 
                choices={[
                  ["Long Bombardment",
                  <Unit
                    parentCallback={this.switchUnit}
                    heading="How to Attack"
                    body="Now you have to decide how to get across 700 yards of no man's land.  You need to decide what formation and where to put your machine guns."
                    choices={[
                      ["Large mass protecting Machine Gun platoon",
                      this.WireUnit(choices11),
                      "Your training suggests that you place your troops in a large mass and charge directly across with your machine guns protected like this:"],
                      ["Machine Guns out front",
                      this.WireUnit(choices12),
                      "You can also put the machine guns out front to try to force the German soldiers heads down with a large mass attack:"],
                      ["Machine Guns out front",
                      this.WireUnit(choices13),
                      "Your French advisor suggests that you send your troops across with a platoon stopped and firing while the rest make a more dispersed approach and move in “lanes”. Your machine guns will be to the rear, like so:"],
                      ["Slip across No Man's Land",
                      this.WireUnit(choices14),
                      "Your final option is to split your troops into tiny clumps and have them try to sneak across no man's land:"]]}/>,
                  "Your American training and the recommendation of your French advisor would suggest that you use a massive 12 hour bombardment designed to obliterate the German lines and to use mustard and chlorine gas to kill as many Germans as possible. The problem is that this makes it clear where you will attack."],
                  ["Limited Bombardment",
                  <Unit
                    parentCallback={this.switchUnit}
                    heading="How to Attack"
                    body="Now you have to decide how to get across 700 yards of no man's land.  You need to decide what formation and where to put your machine guns."
                    choices={[
                      ["Large mass protecting Machine Gun platoon",
                      this.WireUnit(choices21),
                      "Your training suggests that you place your troops in a large mass and charge directly across with your machine guns protected like this:"],
                      ["Machine Guns out front",
                      this.WireUnit(choices22),
                      "You can also put the machine guns out front to try to force the German soldiers heads down with a large mass attack:"],
                      ["Machine Guns out front",
                      this.WireUnit(choices23),
                      "Your French advisor suggests that you send your troops across with a platoon stopped and firing while the rest make a more dispersed approach and move in “lanes”. Your machine guns will be to the rear, like so:"],
                      ["Slip across No Man's Land",
                      this.WireUnit(choices24),
                      "Your final option is to split your troops into tiny clumps and have them try to sneak across no man's land:"]]}/>,
                  "You could also decide to use a lot of guns for just a few minutes. This should cause problems and German losses in the front two trenches as well as cutting wire but not give Germans the chance to reinforce this position."],
                  ["Sneak Across",<Unit
                  parentCallback={this.switchUnit}
                  heading="How to Attack"
                  body="Now you have to decide how to get across 700 yards of no man's land.  You need to decide what formation and where to put your machine guns."
                  choices={[
                    ["Large mass protecting Machine Gun platoon",
                    this.WireSneak(choices31),
                    "Your training suggests that you place your troops in a large mass and charge directly across with your machine guns protected like this:"],
                    ["Machine Guns out front",
                    this.WireSneak(choices32),
                    "You can also put the machine guns out front to try to force the German soldiers heads down with a large mass attack:"],
                    ["Machine Guns out front",
                    this.WireSneak(choices33),
                    "Your French advisor suggests that you send your troops across with a platoon stopped and firing while the rest make a more dispersed approach and move in “lanes”. Your machine guns will be to the rear, like so:"],
                    ["Slip across No Man's Land",
                    this.WireSneak(choices34),
                    "Your final option is to split your troops into tiny clumps and have them try to sneak across no man's land:"]]}/>,
                  "Your longest serving soldier who fought natives 30 years ago suggests not using artillery and to try to sneak across no mans land."]]}/>,
                "Click This Link to make your first decision"]]}/>,
              "Follow the link to learn your orders"
          ]
        ]}//we all good here
      />
        
    };
    this.state = Object.assign({}, this.defaultstate);

    this.switchUnit = this.switchUnit.bind(this);
    this.reset = this.reset.bind(this);
  }


  
  

  switchUnit = (unit) =>{
    this.setState({current:unit});
  }

  reset = () =>{
    this.setState(this.defaultstate);
  }

  WireUnit(choices){ //done
    return <Unit 
        heading="The Wire" 
        parentCallback={this.switchUnit} 
        rng={true} 
        choices={[[choices[0],this.Cut(choices[2]),"If you roll a "+choices[0]+", click here."],[choices[1],this.NotCut(),"If you roll a "+choices[1]+", click here."]]} 
        body="You have made it across most of no man’s land with modest losses. Light fire coming from line 2 and the bunker. Little fire is coming from the front German line."/>;
    
  }

  WireSneak (choices) {
      return <Unit 
        heading="The Wire" 
        parentCallback={this.switchUnit} 
        rng={true} 
        choices={[[choices[0],this.Cut(choices[2]),"If you roll a "+choices[0]+", click here."],[choices[1],this.NotCut(),"If you roll a "+choices[1]+", click here."]]} 
        body="Your first obstacle is the wire belt. You are gambling that you can get through it and into the trenches before the Germans realize you are coming. Failure to get through the wire will be devastating in this formation. Let us see if you get lucky."/>;
  }
  
  Cut (choices){ //done
    return <Unit 
        heading="The Wire Is Cut" 
        parentCallback={this.switchUnit} 
        rng={true} 
        choices={[[choices[0],this.Trench1(choices[2]),"If you roll a "+choices[0]+", click here."],[choices[1],this.TrenchDead(),"If you roll a "+choices[1]+", click here."]]} 
        body = "Gaps have been quietly cut through the wire. The German’s are unaware until you get within 50 feet of the front line. Combat will be fast, and close. Time to push on to the next obstacle, the first German line."/>
    
  
  }
  
  Trench1(choices) {
      return <Unit 
        heading="The First Trench" 
        parentCallback={this.switchUnit} 
        choices={[["Push forward immediately",this.Bunker(choices),null],["Wait, reorganize, then attack",this.Bunkerb(choices[2]),null]]} 
        body="You arrive at the first German trench. Your bombardment has clearly had a large impact. There are a number of dead and wounded Germans but there are many more prisoners, who were not touched by the shells but are suffering from burst ear drums and shock. Very few Germans in this line put up a fight and most of those who did headed for the second line once you got your men through the wire. You are starting to take some fire from the second line, but your objective, the bunker, is putting out a large amount of machine gun fire.\nYour men are somewhat tired and and your formations have lost some cohesion (organization)."/>;
    
  }
  
  Bunker(choices) {
      return <Unit 
        heading="Objective The Bunker" 
        parentCallback={this.switchUnit} 
        rng={true}
        choices={[[choices[0],this.BunkerFalls(choices[2]),"If you roll a "+choices[0]+", click here."],[choices[1],this.BunkerHolds(choices[2]),"If you roll a "+choices[1]+", click here."]]} 
        body="The bunker machine guns open heavy fire on you and your men and your losses begin to mount. Germans try to hold the trench and inflict even more losses. Your command manages to push into the trench. Remains of Germans killed in the fighting are scattered about and some prisoners many wounded are taken. Before you can give orders your well trained men begin to move against the bunker. They are trying to get close enough to throw grenades into the firing slits. A dozen of your troops go down in seconds."/>;

  }
  
  Bunkerb(choices){//done
    return <Unit 
        heading="Objective The Bunker" 
        parentCallback={this.switchUnit} 
        rng={true}
        choices={[[choices[0],this.BunkerHolds(choices[2]),"If you roll a "+choices[0]+", click here."],[choices[1],this.BunkerFalls(choices[2]),"If you roll a "+choices[1]+", click here."]]} 
        body="The bunker machine guns open heavy fire on you and your men and your losses begin to mount. Germans try to hold the trench and inflict even more losses. Your command manages to push into the trench. Remains of Germans killed in the fighting are scattered about and some prisoners many wounded are taken.\nBefore you can give orders your well trained men begin to move against the bunker. They are trying to get close enough to throw grenades into the firing slits. A dozen of your troops go down in seconds."/>;
    
  }
  
  BunkerFalls(choices) {
      return <Unit 
        heading="The Bunker Falls" 
        parentCallback={this.switchUnit} 
        choices={[["Stay and hold the bunker and Trench Line 2",this.GermanCA(choices),null],["Collect your troops and attack Trench 3",this.Trench3(choices),null]]} 
        body="Despite clearly heavy losses you have taken the bunker. Your leading platoons have suffered heavily but platoons 3 and 4 and the machine gun unit is still very effective. The enemy is delivering heavy fire from trench line three which has its own bunkers. Do you stay here at the bunker and trench line 2 and prepare to hold it, or do you collect your troops and order an attack on Trench 3?"/>;
  }
  
  GermanCA (choices) { //done
    return <Unit
        heading="The German's Counterattack" 
        parentCallback={this.switchUnit} 
        rng={true}
        choices={[[choices[0],this.FYTWIO(),"If you roll a "+choices[0]+", click here."],[choices[1],this.You(choices[2]),"If you roll a "+choices[1]+", click here."]]} 
        body="Your newly captured bunker was engineered in such a way that you can not use it to fire in the direction of the Germans, who are launching an attack from their third line to try to drive you out of trench line 2 and back across no man’s land. You have your machine guns and your company is reorganized. If you can hold for the next 8 hours other troops (French) will arrive and take over for you."/>;
  
    
  }
  
  Trench3(choices) { //done
    return <Unit 
        heading="Attack on Trench Line #3" 
        parentCallback={this.switchUnit} 
        rng={true}
        choices={[[choices[0],this.Trench3Dead(),"If you roll a "+choices[0]+", click here."],[choices[1],this.GermanCA(choices[2]),"If you roll a "+choices[1]+", click here."]]} 
        body="Trench line #3 has more troops and many more bunkers. The Germans were fairly safe in the bunkers during your bombardment and suffered few losses. The amount of time it took you to get this far has allowed the German’s to bring up troops from line #4. Your men are falling in large numbers but they press on getting into the trench. "/>;
      
  }
  
  FYTWIO (choices) { //done
    return <Unit 
        heading="For You, the War is Over" 
        parentCallback={this.reset} 
        choices={[["Try again"]]} 
        body="You were attacked by superior numbers, and while your men held those to your left ran away, that allowed German troops to get behind you. The arrival of a German tank was the end. You surrender to a German officer who greets you with, “Smile American for you the war is over”. You and most of your men who were not killed will be sent to a German prison. You guess maybe 50 of your men got away and were able to get back to friendly lines. The rest were killed or captured."/>
    
  }
  
  You (choices) { //done
    return <Unit 
        heading="You" 
        rng={true}
        parentCallback={this.switchUnit} 
        choices={[[choices[0],this.YouRDead(),"If you roll a "+choices[0]+", click here."],[choices[1],this.YouRWounded(),"If you roll a "+choices[1]+", click here."],[choices[2],this.YouWin(),"If you roll a "+choices[2]+", click here."]]} 
        body="Before we can see how your command did we need to see if you lived long enough to find out."/>
    
  }
  
  YouRDead (choices) { //done
    return <Unit 
        heading="You are Dead" 
        parentCallback={this.reset} 
        choices={[["Try again"]]} 
        body="Did we win? Who knows because you did not live to see it."/>
    
  }
  
  YouRWounded(choices) {
    return <Unit
        heading="You are Wounded" 
        parentCallback={this.switchUnit} 
        choices={[["Click here to see the outcome", this.YouWin(), ""]]} 
        body="You have been hit and will be spending the next few months in a hospital in France, until you are 1) recovered enough to return to your unit 2) recovered enough to be sent either to a base in France or the United States to train troops or perform other “light” duties or 3) You will be returned to the United States for more intensive care and a disability discharge."/>
    
  }
  
  YouWin(choices) { //done
    return <Unit 
        heading="You Accomplished Your Objective" 
        parentCallback={this.reset} 
        choices={[["Try again"]]} 
        body="You took your objective and held, mission accomplished. You and your company were pulled out before dawn the day after and can expect 12-48 hours rest before being assigned a new mission."/>
    
  }
  
  BunkerHolds(choices) { //done
    return <Unit 
        heading="The Bunker Holds" 
        parentCallback={this.reset} 
        choices={[["Try again"]]} 
        body="Your troops are unable to knock out the bunker. The Germans in Trench #3 are clearly massing to attack you. You can not hold this Trench #2 if the Germans still operate the bunker. You order your men to retreat back to German Trench #1 and prepare to try and hold it there. The Bunker machine guns cause large casualties as you retreat."/>;
    
  }
  
  TrenchDead(choices) { //done
    return <Unit 
        heading="The First Trench" 
        parentCallback={this.reset} 
        choices={[["Try again"]]} 
        body="You arrive at the first German trench. The fighting is close and hand to hand. The outnumbered Germans pull out of the trench in surprisingly good order. These are veterans, who fall back only a few yards and open fire. Your men are tired and and your formations have lost nearly all cohesion (organization). The moment it is clear to the Germans that you control the trench they open with heavy artillery that has been pre-ranged on the trench. Very heavy artillery fire with high explosives and gas hit you hard along with machine gun fire from the bunker. You are taking very heavy losses and can not remain here under this fire. The German soldiers you just drove out of the trench are coming back with more help behind. You have no time to give orders. Your men break in disorder for the rear. Your attack has failed. 63 men under your command are now dead or prisoners. Another 39 have suffered injuries, You have accomplished nothing, but you will get to try again in a few minutes."/>;
    
  }
  
  Trench3Dead(choices)  { //done
    return <Unit 
        heading="Attack on Trench #3 Fails" 
        parentCallback={this.reset} 
        choices={[["Try again"]]} 
        body="You are unable to take the trench. Strong German forces immediately follow you out and you are unable to hold either Trench #2 or Trench #1. You're finally saved by French troops inside the allied line. You have failed."/>;
    
  }
  
  NotCut(choices) { //done
    return <Unit  
        heading="The Wire Is Not Cut" 
        parentCallback={this.reset}
        choices={[["Try again"]]} 
        body="The artillery failed to cut the wire. You can not get through it before German reinforcements arrive and open heavy fire. You order a retreat but get caught by light German artillery and some gas before you return to your lines.\nThe attack FAILS. Still your dispersed approach has reduced your losses. You have led 3 of your men to their deaths and some 13 have been hit, a few will be disabled for life."/>;
    
  }



  render() {
    let t = {}
    t = Object.assign(t,this.state.current);
    console.log(t)
    return (
      <div className="App">
        <div>
        {t}
        </div>

      </div>
    )
  }

  



}

function getRandomInt (min, max) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}


export default App;
